// 导入 eventBus 模块
import bus from './eventBus.js';
import { getSongListClassification, getHotSongListClassification, getSongList } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 歌单大分类
      categories: [],
      // 歌单小分类
      subs: [],
      // 语种，风格，场景，情感，主题大分类
      languageCgr: [],
      styleCgr: [],
      sceneCgr: [],
      emotionCgr: [],
      themeCgr: [],
      // 右上角热门的歌单分类
      hotTags: [],
      // 分类
      cat: '全部',
      // 渲染的歌单
      playlists: [],
      // 是否显示分类选项卡片
      isTagsBox: false,
      // 默认每次请求 100个歌单
      limit: 100,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 歌单总数
      total: 0
    };
  },

  async mounted() {
    // 获取歌单分类
    let res = await getSongListClassification(); // console.log(res);
    // 保存歌单大分类

    this.categories = res.data.categories; // console.log(this.categories);
    // 保存歌单小分类

    this.subs = res.data.sub.map(sub => sub.name); // console.log('歌单小分类', this.subs);
    // 把歌单小分类放到对应歌单大分类

    this.classify(); // 获取热门的歌单分类

    let res1 = await getHotSongListClassification(); // console.log(res1);
    // console.log(res1.data.tags);

    this.hotTags = res1.data.tags.map(tag => tag.name); // console.log('热门歌单分类', this.hotTags);
    // 获取歌单 (网友精选碟)

    this.getSongLists(); // 组件创建时添加分类选项卡片监听器

    window.addEventListener('click', this.otherClose);
  },

  beforeDestroy() {
    /* 组件销毁的时候要移除侦听器 */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // 点击右上角热门分类更换歌单列表
    change(item) {
      // console.log(item, this.cat);
      this.cat = item; // 数据发送方通过 eventBus 把 highQualityCat 传给 HighQualitySongList 组件

      bus.emit('highQualityCat', this.cat); // 获取歌单 (网友精选碟)

      this.getSongLists();
    },

    // 点击分类选项卡片更换歌单列表(没有实参时 形参取默认值)
    tagsChange(tag = '全部') {
      this.cat = tag;
      bus.emit('highQualityCat', this.cat);
      this.getSongLists();
      this.isTagsBox = false;
    },

    // 获取歌单 (网友精选碟) 默认每次请求每页一百个歌单
    async getSongLists() {
      let res = await getSongList(this.cat, this.limit, this.offset); // console.log('获取歌单 (网友精选碟)', res);

      this.total = res.data.total;
      this.playlists = res.data.playlists; // console.log('this.playlists', this.playlists);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新获取歌单

      this.getSongLists();
    },

    // 显示播放键
    playKeyMouseover(index) {
      this.$refs.playKey[index].style.display = "block";
    },

    // 隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].style.display = "none";
    },

    // 显示与隐藏分类选项卡片
    changeIsTagsBox() {
      this.isTagsBox = !this.isTagsBox;
    },

    // 点击其它地方，分类选项卡片关闭
    otherClose(e) {
      // 需要打开了分类选项卡片才能使用 tags_box.contains() 方法，所以要加上 this.isTagsBox  == true 条件
      if (this.isTagsBox == true && this.$refs.tags_box.contains(e.target)) {// console.log('在分类选项卡片中');
      }

      if (this.isTagsBox == true && !this.$refs.tags_box.contains(e.target) && e.target != this.$refs.options_left) {
        // console.log('正在点击其它地方!关闭分类选项卡片');
        this.isTagsBox = false;
      }
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 把歌单小分类放到对应歌单大分类
    classify() {
      // 语种，按顺序加入数组
      this.languageCgr.push(this.subs[3], this.subs[8], this.subs[13], this.subs[17], this.subs[22]); // console.log(this.languageCgr);
      // 风格

      this.styleCgr.push(this.subs[1], this.subs[7], this.subs[12], this.subs[16], this.subs[24], this.subs[25], this.subs[30], this.subs[36], this.subs[38], this.subs[43], this.subs[45], this.subs[49], this.subs[54], this.subs[56], this.subs[59], this.subs[60], this.subs[63], this.subs[64], this.subs[65], this.subs[66], this.subs[67], this.subs[68], this.subs[69]); // console.log(this.styleCgr);
      // 场景

      this.sceneCgr.push(this.subs[4], this.subs[6], this.subs[14], this.subs[19], this.subs[20], this.subs[28], this.subs[32], this.subs[35], this.subs[40], this.subs[44], this.subs[48], this.subs[51]); // console.log(this.sceneCgr);
      // 情感

      this.emotionCgr.push(this.subs[5], this.subs[9], this.subs[11], this.subs[21], this.subs[27], this.subs[29], this.subs[34], this.subs[37], this.subs[41], this.subs[46], this.subs[52], this.subs[55]); // console.log(this.emotionCgr);
      // 主题

      this.themeCgr.push(this.subs[0], this.subs[2], this.subs[10], this.subs[15], this.subs[18], this.subs[23], this.subs[26], this.subs[31], this.subs[33], this.subs[39], this.subs[42], this.subs[47], this.subs[50], this.subs[53], this.subs[57], this.subs[58], this.subs[61], this.subs[62]); // console.log(this.themeCgr);
    },

    // 会让 active 类失效
    // // 鼠标放上 hotTag 文字变黑
    // hotTagMouseenter(index) {
    //   if (this.$refs.hotTag[index].style.color != 'rgb(225, 81, 81)') this.$refs.hotTag[index].style.color = '#000'
    // },
    // // 鼠标移开 hotTag 字色恢复
    // hotTagMouseleave(index) {
    //   if (this.$refs.hotTag[index].style.color != 'rgb(225, 81, 81)') this.$refs.hotTag[index].style.color = 'rgb(94, 94, 94)'
    // },
    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    }

  },
  watch: {
    // 标志已请求完数据
    playlists() {
      if (this.playlists.length != 0) this.$emit('SongListSelectedByNetizensLoadingOk', 1);
    }

  }
};