// 导入 eventBus 模块
import bus from './eventBus.js'; // 引入解构辅助函数

import { mapMutations, mapState } from 'vuex';
import { getHighQualityTags, getHighQualitySongList } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 精品歌单分类
      tags: [],
      // 分类
      highQualityCat: '全部',
      // 渲染的歌单
      playlists: [],
      // 是否显示分类选项卡片
      isTagsBox: false,
      //当前分类
      active: -1
    };
  },

  async mounted() {
    // 获取精品歌单标签列表
    let res = await getHighQualityTags(); // console.log(res);
    // map() 用于创建一个新数组，新数组的每个元素都是原始数组经过某种处理后的结果

    this.tags = res.data.tags.map(tag => tag.name); // console.log('精品歌单分类', this.tags);
    // 获取精品歌单

    this.getHighQuality(this.highQualityCat); // 添加分类选项卡片监听器

    window.addEventListener('click', this.otherClose);
  },

  // 写在 created 阶段（现成的解决方案）数据接收方
  created() {
    // 通过 eventBus 接收传过来的值
    bus.on('highQualityCat', val => {
      this.highQualityCat = val; // console.log('传值成功 ', val, this.highQualityCat);
    });
  },

  beforeDestroy() {
    /* 组件销毁的时候要移除侦听器 */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // 获取精品歌单
    async getHighQuality(highQualityCat) {
      let res = await getHighQualitySongList(highQualityCat); // console.log('获取精品歌单', res);
      // 保存歌单列表

      this.playlists = res.data.playlists; // console.log(this.playlists);
    },

    // 显示与隐藏分类选项卡片
    changeIsTagsBox() {
      this.isTagsBox = !this.isTagsBox;
    },

    // 点击其它地方，分类选项卡片关闭
    otherClose(e) {
      // 需要打开了分类选项卡片才能使用 tags_box.contains() 方法，所以要加上 this.isTagsBox  == true 条件
      if (this.isTagsBox == true && this.$refs.tags_box.contains(e.target)) {// console.log('在分类选项卡片中');
      }

      if (this.isTagsBox == true && !this.$refs.tags_box.contains(e.target) && e.target != this.$refs.box_top_right) {
        // console.log('正在点击其它地方!关闭分类选项卡片');
        this.isTagsBox = false;
      }
    },

    // 点击分类选项卡片更换歌单列表
    tagsChange(tag, index) {
      this.highQualityCat = tag;
      bus.emit('highQualityCat', this.highQualityCat);
      this.getHighQuality(this.highQualityCat);
      this.isTagsBox = false;
      this.active = index;
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 显示播放键
    headMouseover(index) {
      // console.log(this.$refs.playKey[index].classList);
      this.$refs.playKey[index].classList = 'playKey';
    },

    // 隐藏播放键
    headMouseleave(index) {
      this.$refs.playKey[index].classList = '';
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 解释如何成为精华歌单
    goExplain() {
      // console.log('location 对象：', location);
      // 利用 location 对象在本页面打开网页
      // location.href = 'https://music.163.com/#/topic?id=202001'
      // 在新页面打开
      window.open('https://music.163.com/#/topic?id=202001');
    },

    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateBoutiqueSongList'])
  },
  watch: {
    // 本文监听$route为CSDN博主「写bug断了电」原文链接：https://blog.csdn.net/fyydashen/article/details/110898645
    $route(to, from) {
      if (to.path == "/DiscoverMusic/DiscoverSongSheet" && this.$store.state.isBoutiqueSongList == true) {
        // console.log("从 " + from.path + " 页面来");
        // console.log("到 " + to.path + " 页面去");
        // console.log('回到歌单页！隐藏精品歌单列表');
        this.updateBoutiqueSongList(0);
      }
    },

    // 监听从组件 SongListSelectedByNetizens 传过来的 highQualityCat 值
    highQualityCat() {
      // console.log('highQualityCat 改变为：', this.highQualityCat);
      // 先检测再获取
      if (this.highQualityCat == '全部') {
        this.getHighQuality(this.highQualityCat);
      } else {
        // 检测传过来的 highQualityCat 值是否属于精品歌单的分类
        let r = this.tags.indexOf(this.highQualityCat);

        if (r != -1) {
          this.getHighQuality(this.highQualityCat);
        }

        if (r == -1) {}
      }
    }

  }
};