// 导入子组件
import HighQualitySongList from '@/components/menu/DiscoverMusic/DiscoverSongSheet/HighQualitySongList.vue';
import SongListSelectedByNetizens from '@/components/menu/DiscoverMusic/DiscoverSongSheet/SongListSelectedByNetizens.vue';
import BoutiqueSongList from '@/components/menu/DiscoverMusic/DiscoverSongSheet/BoutiqueSongList.vue';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      loadingOk1: 0,
      loadingOk2: 0
    };
  },

  mounted() {},

  components: {
    HighQualitySongList,
    SongListSelectedByNetizens,
    BoutiqueSongList
  },
  methods: {
    HighQualitySongListLoadingOk(isOk) {
      this.loadingOk1 = isOk;
    },

    SongListSelectedByNetizensLoadingOk(isOk) {
      this.loadingOk2 = isOk;
    }

  },
  watch: {
    loadingOk2() {
      // 两个组件都已请求完数据 则设置 loadingOk 属性都为 1
      this.isLoading = this.loadingOk1 == 1 && this.loadingOk2 == 1 ? 0 : 1;
    }

  }
};