// 导入 eventBus 模块
import bus from './eventBus.js'; // 引入解构辅助函数

import { mapMutations, mapState } from 'vuex';
import { getHighQualityTags, getHighQualitySongList } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 精品歌单分类
      tags: [],
      // 分类
      highQualityCat: '全部',
      // 渲染的歌单
      playlists: [],
      // 只渲染获取到的歌单的首歌单
      firstPlaylists: [],
      // 是否显示该组件
      isShow: 1
    };
  },

  async mounted() {
    // 获取精品歌单标签列表
    let res = await getHighQualityTags(); // console.log(res);

    this.tags = res.data.tags.map(tag => tag.name); // console.log('精品歌单分类', this.tags);
    // 获取精品歌单

    this.getHighQuality(this.highQualityCat);
  },

  // 写在 created 阶段（现成的解决方案）数据接收方
  created() {
    // 通过 eventBus 接收传过来的值
    bus.on('highQualityCat', val => {
      this.highQualityCat = val; // console.log('传值成功 ', val, this.highQualityCat);
    });
  },

  methods: {
    // 获取精品歌单
    async getHighQuality(highQualityCat) {
      let res = await getHighQualitySongList(highQualityCat); // console.log('获取精品歌单', res);
      // 保存歌单列表

      this.playlists = res.data.playlists; // console.log(this.playlists);
      // 提取列表首歌单

      this.firstPlaylists = res.data.playlists[0]; // console.log(this.firstPlaylists);
    },

    // 去往精品歌单列表
    goto() {
      this.updateBoutiqueSongList(true);
    },

    // 在 methods 中解构 store 里的 index.js 里的 mutations 传过来的方法
    ...mapMutations(['updateBoutiqueSongList'])
  },
  watch: {
    // 监听从兄弟组件 SongListSelectedByNetizens 传过来的 highQualityCat 值
    highQualityCat() {
      // 先检测再获取精品歌单
      if (this.highQualityCat == '全部') {
        this.isShow = 1;
        this.getHighQuality(this.highQualityCat);
      } else {
        // 检测传过来的 highQualityCat 值是否属于精品歌单的分类
        let r = this.tags.indexOf(this.highQualityCat);
        this.isShow = r == -1 ? -1 : 1;
        if (r !== -1) this.getHighQuality(this.highQualityCat);
      }
    },

    // 标志已请求完数据
    firstPlaylists() {
      if (this.firstPlaylists.length !== 0) this.$emit('HighQualitySongListLoadingOk', 1);
    }

  }
};